.menu {
    cursor: pointer;
}

.line {
    fill: none;
    stroke: var(--text);
    stroke-width: 6;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
    stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}

.line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
}

.line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6;
}

.line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
}

.opened .line1 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
}

.opened .line2 {
    stroke-dasharray: 1 60;
    stroke-dashoffset: -30;
    stroke-width: 6;
}

.opened .line3 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
}

.menu-responsive {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 100%;
    background-color: var(--background);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: 0.5s;
    z-index: 1000000000000000;
}

.menu-responsive > .menu.mode-toggle {
    position: absolute;
    top: 20px;
    right: 20px;
}

.menu-responsive > .options > a {
    width: 90%;
    outline: none;
    border: none;
    padding: 15px;
    color: var(--text);
    background-color: transparent;
    font-size: 20px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 15px;
    margin-bottom: 5px;
    text-decoration: none;
    text-align: center;
}

.menu-responsive > .options > a:hover {
    background-color: var(--profile-drop-down-background-hover);
}



