.new-paste {
    padding: 0 40px;
}

.new-paste > h1 {
    margin-bottom: 20px;
}

.CodeMirror * {
    font-family: 'Roboto Mono' !important;
    font-size: 17px;
}

.CodeMirror {
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    padding: 10px;
    border-radius: 25px;
    margin-bottom: 50px;
}

.options {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 50px;
}

.option {
    width: calc(100% / 4 - 20px);
}

.option > h1 {
    margin-bottom: 15px;
}

.option > h1 > span {
    font-size: 15px;
    direction: ltr;
    color: var(--text-muted);
}

.option > input, select {
    background-color: var(--code-background);
    outline: none;
    border: none;
    width: 100%;
    height: 70px;
    padding: 10px 15px;
    box-shadow: var(--profile-drop-down-background-shadow) 0px 4px 12px;
    color: var(--text);
    border-radius: 20px;
    font-size: 15px;
    transition: 0.5s;
