.editable-area {
    position: relative;
}
.editable-area > svg {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 0;
    left: 0;
}

.editable-area > input {
    border: none;
    outline: none;
    border-bottom: 1px solid red;
    background-color: transparent;
    transition: 0.5s;
}